*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  font-size: 10px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}
.form-container {
  min-height: 50rem;
  width: 50rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(0, 0, 0, 0.7);
  padding: 2rem;
  border-radius: 4rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.form-container h1 {
  border-bottom: 1px solid #e3e3e3;
  border-top: 1px solid #e3e3e3;
  color: #fff;
  white-space: nowrap;
  font-size: 4rem;
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translate(-50%, -50%);
}
form {
  width: 80%;
  height: 100%;
  margin: 8rem 0;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.label-group {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 0.15rem 0;
}
.label-group > label {
  font-size: 1.8rem;
  font-weight: 600;
  color: #e3e3e3;
  white-space: nowrap;
  text-align: right;
  margin-right: 2rem;
}
.label-group > input {
  flex-grow: 1;
  overflow: hidden;
  background: transparent;
  border:none;
  border-bottom: 1.5px solid #e3e3e3;
  color: #e3e3e3;
}
.label-group > input:focus{
  border-bottom: 1.5px solid var(--green-color);
}
button {
  position: absolute;
  bottom: 5%;
  font-size: 2.5rem;
  border: none;
  padding: 0.5rem 2rem;
  border-radius: 2.5rem;
  color: #000;
  cursor: pointer;
}
.prev {
  background-color: var(--red-color);
  left: 30%;
}
.next {
  background-color: var(--green-color);
  right: 30%;
}
.save {
  background-color: var(--yellow-color);
  right: 30%;
}
